import {ActionType, ActionCreator} from './actions';
import NameSpace from '../name-space';
import {extend} from '../../utils';

const NAME_SPACE = NameSpace.DATA;

const initialState = {
  code: null,
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case ActionType.CODE:
      return extend(state, {code: action.payload});
  }

  return state;
};

const Operation = {
  setCode: (code) => (dispatch, getState) => {
      dispatch(ActionCreator.setCode(code));

      var myHeaders = new Headers();
      myHeaders.append("Content-Type", "application/x-www-form-urlencoded");
      myHeaders.append("Cookie", "csrftoken=Bf4BIN3poundd3ew4hHsCAyTbCGP89ef; rur=FTW");

      var urlencoded = new URLSearchParams();
      urlencoded.append("client_id", "1576420319199590");
      urlencoded.append("client_secret", "f1633dbb351aac66a73d763e06497079");
      urlencoded.append("grant_type", "authorization_code");
      urlencoded.append("redirect_uri", "https://volokhovoleg.github.io/");
      urlencoded.append("code", code);

      var requestOptions = {
          method: 'POST',
          headers: myHeaders,
          body: urlencoded,
          redirect: 'follow'
      };

      fetch("https://api.instagram.com/oauth/access_token", requestOptions)
          .then(response => response.text())
          .then(result => {
              const {access_token, user_id} = result;

              fetch(`https://graph.instagram.com/${user_id}?fields=id,username,media_type,media_url&access_token=${access_token}`)
                  .then((response) => {
                      console.log(response);
                  });
          })
          .catch(error => console.log('error', error));
  }
};

export {reducer, Operation, ActionType, ActionCreator};
