import React, { useState, useEffect }  from 'react';
import {connect} from 'react-redux';
import {getCode} from '../../reducer/data/selectors'
import {Operation as DataOperation} from '../../reducer/data/data';

const App = ({setCode, code}) => {
    const redirect = () => {
        fetch(`https://api.instagram.com/oauth/authorize?client_id=1576420319199590&redirect_uri=https://volokhovoleg.github.io/&scope=user_profile,user_media&response_type=code`)
            .then((response) => {
                window.open(response.url);
            })
            .catch((error) => {
                throw error;
            });
    };

    useEffect(() => {
        const checkCode = () =>
            new URLSearchParams(window.location.search).has(`code`)
            && setCode(new URLSearchParams(window.location.search).get(`code`));
        checkCode();
    }, [])

    return <>
      <button onClick={()=> {
          redirect();
      }} type={"button"}>Получить данные инсты</button>
    </>;
};

const mapStateToProps = (state) => ({
    code: getCode(state),
});

const mapDispatchToProps = (dispatch) => ({
    setCode(code) {
        dispatch(DataOperation.setCode(code));
    }
});

export {App};
export default connect(mapStateToProps, mapDispatchToProps)(App);
